body {
  margin: 0;
  background-color: #0f172a;
  font-family: Arial, sans-serif;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.user {
  display: flex;
  align-items: center;
  background: #1e293b;
  padding: 12px 20px;
  border-radius: 12px;
  color: white;
  margin-bottom: 20px;
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.name {
  margin-right: 10px;
}

.mic {
  font-size: 22px;
}

/* ðŸ”Š Speaking animation */
@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.3); }
  100% { transform: scale(1); }
}

.speaking {
  animation: pulse 0.6s infinite;
}

.mute-btn {
  background: #22c55e;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  color: white;
  transition: background 0.3s;
}

.mute-btn:hover {
  background: #16a34a;
}
